<!--  -->
<template>
  <div class="main">
    <el-container class="main_con">
      <el-aside width="23%" class="con_l">
        <div class="con">
          <ul class="tags">
            <template v-for="(item, index) in playlistTags">
              <li :key="index" class="item_list" style="pointer-events: auto;">
                <p class="tip flex_c">
                  <i class="iconfont icon-icon_theme_"></i>
                  <span>{{ item.name }}</span>
                </p>
                <ul class="list_tags flex_c">
                  <template v-for="(tagItem, tagIndex) in item.data">
                    <li :key="tagIndex" class="item" :class="{ active: isActive === index+''+ tagIndex }" @click="isActive =  index+''+ tagIndex;getSelectItem (tagItem)">
                      <span class="name">{{ tagItem.name }}</span>
                    </li>
                  </template>
                </ul>
              </li>
            </template>
          </ul>
        </div>
      </el-aside>
      <el-main class="con_r">
        <div class="title">{{title}}</div>
        <playlist :playlistData="playlistData"></playlist>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import { HttpManager } from "../api";
import Playlist from '../components/Playlist.vue';
import mixin from '../mixins';

export default {
  name: "Playlists",
  mixins: [mixin],
  data () {
    return {
      playlistData: {},
      playlistTags: [],
      selectTagId: 0,
      rcmTag: "new",
      title: "",
      isActive: "00",
    };
  },

  components: {
    Playlist
  },

  methods: {
    getSelectItem (item) {
      if ('type' in item) {
        this.rcmTag = item.type;
      }
      else {
        this.rcmTag = "";
        this.selectTagId = item.id;
      }
    },
    getPlaylist (val) {
      let that = this;
      let promise;
      //自动回顶部
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      if (this.rcmTag != "") {
        promise = HttpManager.getRcmPlaylist({
          order: this.rcmTag,
          pn: val,
          rn: 30
        });
      } else {
        promise = HttpManager.getPlaylist({
          id: this.selectTagId,
          pn: val,
          rn: 30
        });
      }
      promise.then(res => {
        that.playlistData = Object.assign({}, that.playlistData,
          {
            code: parseInt(res.code),
            total: parseInt(res.data.total),
            playlist: res.data.data,
            pageSize: 30,
            getData: that.getPlaylist,
          })
        //   that.code = parseInt(res.code);
        // that.total = ;
        // that.playlist = res.data.data;
      })
        .catch(() => { });
    },
    getTagPlaylist () {
      let that = this;
      HttpManager.getTagPlaylist()
        .then(res => {
          that.playlistTags = res.data;
          that.playlistTags.unshift({
            name: "精选歌单",
            data: [
              { name: "最新", type: "new" },
              { name: "最热", type: "hot" }
            ]
          });
          that.playlistTags.pop();
          that.title =
            that.playlistTags[0].name + " > " +
            that.playlistTags[0].data[0].name;
        })
        .catch(() => { });
    },
  },

  computed: {},
  watch: {
    isActive: {
      handler (newVal, oldVal) {
        // 获取标签类型和具体类别,isActive=类型序号+''+类别序号
        this.title =
          this.playlistTags[parseInt(newVal.substr(0, 1))].name + " > " +
          this.playlistTags[parseInt(newVal.substr(0, 1))].data[parseInt(newVal.substr(1))].name;
        this.getPlaylist(1);
      },
      // immediate: true, //监听首次绑定值
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getTagPlaylist();
    this.getPlaylist(1);
  },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/font";
*,
:after,
:before {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}
li {
  list-style: none;
}
.main {
  margin-top: 20px;
  padding: 0 120px;
  display: block;
}
.main_con {
  min-width: 750px;
  height: 100%;
  font-size: 14px;
  line-height: 1;
  font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
    sans-serif;
  .con_l {
    min-height: 300px;
    min-width: 200px;
    max-width: 330px;
    background: #fff;
    margin-right: 1%;
    width: 18%;
    // box-shadow: 0 0 30px 0 rgb(65 67 70 / 10%);
    .con {
      overflow: hidden;
      margin: 5px;
      box-shadow: 0 0 20px 0 rgb(65 67 70 / 10%);

      .tags {
        margin-top: 20px;
        max-height: 540px;
        overflow: auto;
        position: relative;
        .item_list {
          padding-left: 10px;
          .tip {
            margin-bottom: 16px;
            color: #000;

            span {
              margin-left: 2px;
              font-weight: 600;
            }
          }
          .list_tags {
            flex-wrap: wrap;
            padding-bottom: 14px;
            .item {
              padding: 6px 18px;
              background: #f7f7f7;
              border-radius: 16px;
              margin: 0 10px 10px 0;
              cursor: pointer;
              &.active,
              &:hover {
                background: #31c27c;
              }
              &.active {
                span {
                  color: #333;
                  font-weight: 600;
                }
              }
              span {
                line-height: 20px;
                font-weight: 300;
                color: #000;
              }
            }
          }
        }
      }
    }
  }
  .con_r {
    position: relative;
    width: 76.14%;
    z-index: 10;
    padding: 0 10px 0;
    .title {
      display: inline-block;
      margin: 20px 0 40px;
      height: 30px;
      line-height: 30px;
      font-size: 28px;
      font-weight: 600;
    }
    .name {
      margin-top: 10px;
      font-size: 16px;
      line-height: 22px;
      font-weight: 400;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      word-break: break-all;
      span {
        cursor: pointer;
      }
    }
    .count {
      color: #999;
      margin-top: 5px;
      margin-bottom: 26px;
      font-size: 14px;
    }
  }
}
.flex_c {
  display: flex;
  align-items: center;
}
</style>



